import { FC, createElement, HTMLAttributes, ReactNode } from 'react'
import clsx from 'clsx'
import { TypographyProps, TypographyType } from './types'
import './Typography.scss'

const Typography: FC<TypographyProps & HTMLAttributes<ReactNode>> = ({
  className = '',
  component = 'p',
  type = 'body',
  text,
  children,
  style = {},
  ...props
}) => {
  const getClassName = (typographyType: TypographyType): string => {
    switch (typographyType) {
      case 'Title1':
      case 'bigTitle':
        return clsx(className, 'Typography__Title', 'Typography__Title_Big')

      case 'Title2':
      case 'title':
        return 'Typography__Title'

      case 'Title3':
      case 'smallTitle':
        return clsx('Typography__Title', 'Typography__Title_Small')

      case 'Headline':
      case 'headline':
        return 'Typography__Headline'

      case 'Subtitle1':
      case 'subtitle':
        return 'Typography__Subtitle'

      case 'Subtitle1None':
      case 'strikethroughSubtitle':
        return clsx(
          'Typography__Subtitle',
          'Typography__Subtitle_Strikethrough',
        )

      case 'Subtitle2':
      case 'smallSubtitle':
        return clsx('Typography__Subtitle', 'Typography__Subtitle_Small')

      case 'Body1':
      case 'body':
        return 'Typography__Body'

      case 'Body1B':
      case 'boldBody':
        return clsx('Typography__Body', 'Typography__Body_Bold')

      case 'Body1None':
      case 'strikethroughBody':
        return clsx('Typography__Body', 'Typography__Body_Strikethrough')

      case 'Body2':
      case 'smallBody':
        return clsx('Typography__Body', 'Typography__Body_Small')

      case 'Body2B':
      case 'smallBodyBold':
        return clsx(
          'Typography__Body',
          'Typography__Body_Small',
          'Typography__Body_Bold',
        )

      case 'Body2None':
        return clsx(
          'Typography__Body',
          'Typography__Body_Small',
          'Typography__Body_Strikethrough',
        )

      case 'Caption1Bold':
        return clsx('Typography__Caption', 'Typography__Caption_Bold')

      case 'Caption1Regular':
        return 'Typography__Caption'

      case 'Caption1None':
        return clsx('Typography__Caption', 'Typography__Caption_Strikethrough')

      case 'Caption2Bold':
        return clsx(
          'Typography__Caption',
          'Typography__Caption_Small',
          'Typography__Caption_Bold',
        )

      case 'Caption2Regular':
        return clsx('Typography__Caption', 'Typography__Caption_Small')
      case 'Caption2None':
        return clsx(
          'Typography__Caption',
          'Typography__Caption_Small',
          'Typography__Caption_Strikethrough',
        )

      case 'HeadingH1':
        return clsx('Typography__Heading', 'Typography__Heading_H1')

      case 'HeadingH2':
        return clsx('Typography__Heading', 'Typography__Heading_H2')

      case 'HeadingH3':
        return clsx('Typography__Heading')

      case 'HeadingH4':
        return clsx('Typography__Heading', 'Typography__Heading_H4')

      case 'HeadingH5':
        return clsx('Typography__Heading', 'Typography__Heading_H5')

      case 'BoldXL':
        return clsx('Typography__XL', 'Typography__XL_Bold')

      case 'RegularXL':
        return clsx('Typography__XL')

      case 'UnderlineXL':
        return clsx('Typography__XL', 'Typography__XL_Underline')

      case 'LinethroughXL':
        return clsx('Typography__XL', 'Typography__XL_Linethrough')

      case 'BoldL':
        return clsx('Typography__L', 'Typography__L_Bold')

      case 'RegularL':
        return clsx('Typography__L')

      case 'UnderlineL':
        return clsx('Typography__L', 'Typography__L_Underline')

      case 'LinethroughL':
        return clsx('Typography__L', 'Typography__L_Linethrough')

      case 'BoldM':
        return clsx('Typography__M', 'Typography__M_Bold')

      case 'RegularM':
        return clsx('Typography__M')

      case 'UnderlineM':
        return clsx('Typography__M', 'Typography__M_Underline')

      case 'LinethroughM':
        return clsx('Typography__M', 'Typography__M_Linethrough')

      case 'BoldS':
        return clsx('Typography__S', 'Typography__S_Bold')

      case 'RegularS':
        return clsx('Typography__S')

      case 'UnderlineS':
        return clsx('Typography__S', 'Typography__S_Underline')

      case 'LinethroughS':
        return clsx('Typography__S', 'Typography__S_Linethrough')

      default:
        return 'Typography__Body'
    }
  }

  return createElement(
    component,
    {
      ...props,
      className: clsx('Typography', className, getClassName(type)),
      style,
    },
    children ?? text ?? null,
  )
}

export default Typography
